// stylelint-disable at-rule-empty-line-before

@import "variables";
@import "functions";
@import "mixins";
@import "sprites";
@import "fonts";
@import "vendor";
@import "base";
@import "helpers";


@keyframes letter { 
	from { 
		opacity: 0; 
	} 
	to { 
		opacity: 1; 
	}  
}
body {
	background-color: $color-gallery;
	font-family: $font-family-roboto;
}

.page{
	margin: 60px auto 100px;

	&__section{
		width: 662px;
		max-width: 100%;
		padding: 30px 22px 0px;
		box-sizing: border-box;
		margin: 2px auto 0px;
		background-color: $color-white;
		background: url("../images/back.jpg");
		background-position: center center;

		@include retina {
        background-image: url("../images/back@2x.jpg");
    	}

		&-javascript {
			padding-top: 60px;
			padding-bottom: 60px;
		}
	}

	&__subtitle {
		color: $color-black;
		font-size: 18px;
		margin: 0;
		margin-bottom: 25px;
	}

	&__link {
		color: $color-cerulean;

		&:hover {
			text-decoration: none;
		}
	}

	@include mobile {
		margin: 0;
	}
}


.bio {
	display: flex;
	position: relative;

	&__portrait {
		width: 140px;
		height: 140px;
		flex-shrink: 0;
		border-radius: 100%;	
		overflow: hidden;	
	}

	&__list {
		padding: 0;
		margin: 0;
		list-style: none;
		width: 100%;
		margin-left: 22px;

		@include mobile {
			margin-left: 0;
		}
	}

	&__item {
		text-transform: uppercase;
		color: $color-dove-gray;
		font-weight: 500;
		font-size: 12px;
		line-height: 1.2;
		display: block;
		height: auto;
		border-bottom: $color-mine-shaft 2px solid;
		width: 100%;
		padding-right: 185px;
		padding-bottom: 4px;
		margin-bottom: 30px;

		@include mobile {
			padding-right: 120px;
		}

	}
	&__approved {
		position: absolute;
		right: 5px;
		bottom: 15px;
		width: 185px;

		@include mobile {
			width: 120px;
		}
	}

	@include mobile {
		flex-direction: column;
		align-items: center;
	}
}


.portrait{

	&__photo {
		width: 100%;
		top: -20%;
		position: relative;
	}
}

.skills {

	&__list {
		padding: 0;
		margin: 0;
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		justify-content: space-between;
		list-style: none;
	}

	&__item {
		width: 140px;
		flex-shrink: 0;
		height: 60px;
	}

	&__checkbox {
		font-size: 12px;
		line-height: 1.2;
		color: $color-dove-gray;
		text-transform: uppercase;
		font-weight: 500;
		padding-left: 27px;
	}
}

.checkbox {
	position: relative;

	&__check {
		display: block;
		position: absolute;
		top: -4px;
		left: 0;
		width: 19px;
		height: 19px;
		border-radius: 5px;
		background-color: #eeeeee;
		border: 2px solid transparent;

		&::before {
			content: "";
			width: 100%;
			height: 100%;
			position: absolute;
			background-image: url("../images/sprites/svg/done.svg");
			background-position: center;
			background-repeat: no-repeat;
			opacity: 0;
			transition: 0.5s ease;
		}
	}

	&:hover {
		cursor: pointer;

		.checkbox__check {
			border: 2px solid $color-cerulean;
		}
	}

	&__input:checked + .checkbox__check {
		background-color: $color-cerulean;

		&::before {
			opacity: 1;
		}
	}
}

.javascript {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	overflow: hidden;

	&__speedometer {
		width: 255px;
		height: 140px;
	}

	&__text {
		margin-left: 67px;

		@include mobile {
			margin-left: 0;
		}
	}

	&__value {
		margin: 0;
	}

	@include mobile {
		flex-direction: column-reverse;
		align-items: center;
	}
}
.speedometer {
	position: relative;

	&__round {
		width: 100%;
		position: absolute;
	}

	&__arrow {
		position: absolute;
		width: 50%;
		bottom: -40px;
		left: 25%;
		transform-origin: 50% 50%;
		transition: 7s $easing-arrow;
	}
}

.js-value {
	font-size: 50px;
	color: $color-cerulean;
	font-weight: 900;
	text-transform: uppercase;
	position: relative;

	&-text:hover {
		cursor: pointer;
		text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5);
	}

	&__span {
		position:absolute;
		top:0px;
		left: 0;
		transition: 0.05s ease;
		
		&-hidden {
			opacity: 0;
		}
	}

}


.visually-hidden {
	width: 1px;
	height: 1px;
	position: absolute;
	margin-left: -1px;
	overflow: hidden;
	opacity: 0;
}